<script>
import MattingUtil from "@/util/MattingUtil";
import mattingUtil from "@/util/MattingUtil";
import ImgUtil from "@/util/ImgUtil";
import FileUtil from "@/util/FileUtil";


export default {
  name: 'CardPhotoView',

  metaInfo: {
    title: '证件照生成工具',
  },

  data() {
    return {
      standards : [
        { label: '一寸 (295x413px)', width: 295, height: 413, ratio: 1.4 },
        { label: '二寸 (413x579px)', width: 413, height: 579, ratio: 1.4 },
      ],
      backgrounds : [
        { label: '白底', value: '#ffffff' },
        { label: '蓝底', value: '#0070c0' },
        { label: '红底', value: '#d0021b' },
      ],

      params : {
        standard : '一寸 (295x413px)',
        width : 295,
        height : 413,
        background : '#0070c0',

      },
      afterImgStyle : {
        source_width : 0,
        source_height : 0,
        width : 0,
        height: 0,
        top : 0,
        left : 0,
      },
      loadModelProcess : {},
      isPreloaded : false,
      saveIng : false,
      imgInfo : {
        img: '',
        imgFile: null,
        snapImg: null,
        snapImgSize: 0,
        snapImgFile: null,
        imgError: false,
        startTime: (new Date()).getTime(),
        endTime: (new Date()).getTime(),
        timer: null,
        snapping: false,
        msg: '',
      },//原图
    };
  },

  computed: {

  },

  mounted() {
    this.checkMatting();
  },

  methods : {
    changeAfterWidth() {
      const rate = this.afterImgStyle.source_width/ this.afterImgStyle.width;
      console.log(rate)
      this.afterImgStyle.height = this.afterImgStyle.source_height / rate;
      console.log('changeAfterWidth');
    },
    changeStandard () {
      for (const standard of this.standards) {
        if (this.params.standard === standard.label)
        {
          this.params.width = standard.width;
          this.params.height = standard.height;
          break;
        }
      }

    },
    loadSnapImg (event) {
      const imgElement = event.target;

      // 示例：获取图片的自然尺寸
      const width = imgElement.naturalWidth;
      const height = imgElement.naturalHeight

      this.afterImgStyle.source_width = width
      this.afterImgStyle.source_height = height

      console.log(this.afterImgStyle)
      const rate = this.afterImgStyle.source_width / this.params.width;

      this.afterImgStyle.height = this.afterImgStyle.source_height / rate;
      this.afterImgStyle.width = this.params.width;

      this.afterImgStyle.top = this.params.height - this.afterImgStyle.height;


    },
    async saveFile(){
      this.saveIng = true;
      try {
        //将某个元素 转化成图片，下载！
        const file = await ImgUtil.elementToImage('#card-photo-container');
        await FileUtil.download(file,'证件照.tool.4yop.com.png')
        this.$message.success('保存成功');
      }  catch (e) {
        console.log(e);
        this.$message.error('出错:'+e.message)
      } finally {
        this.saveIng = false;
      } 
    },
    async removeBg() {
      this.imgInfo = await mattingUtil.snap(this.imgInfo);
      this.afterImgStyle = {
        source_width : 0,
        source_height : 0,
        width : 0,
        height: 0,
        top : 0,
        left : 0,
      };
    },
    handleUploadChange(file) {
      const rawFile = file.raw || file;
      if (!rawFile.type.startsWith('image/'))
      {
        return this.$alert('必须选择的是图片');
      }
      if (rawFile.type.startsWith('image/gif'))
      {
        return this.$alert('不能用gif图');
      }

      let imgInfo = {
        img: URL.createObjectURL(rawFile),
        imgFile: rawFile,
        snapImg: null,
        snapImgSize: 0,
        snapImgFile: null,
        imgError: false,
        startTime: (new Date()).getTime(),
        endTime: (new Date()).getTime(),
        timer: null,
        snapping: false,
        msg: '',
      };
      this.imgInfo = imgInfo;
    },


    checkMatting()
    {
      const t = setInterval(()=>{
        this.isPreloaded = MattingUtil.getIsPreloaded();
        this.loadModelProcess = MattingUtil.getProcess();
        if (this.isPreloaded) clearInterval(t);
      },3000);
    }

  },

  watch: {

  }
};
</script>

<template>
  <div class="card-photo-container">
    <el-card class="box-card" :body-style="{ padding: '0px' }">

      <div class="top-controls">
        <span class="control-label">证件要求:</span>
        <el-select
            v-model="params.standard"
            size="small"
            style="width: 120px; margin-right: 10px;"
            @change="changeStandard"
            :disabled="saveIng"
        >
          <el-option
              v-for="item in standards"
              :key="item.label"
              :label="item.label"
              :value="item.label"
          ></el-option>
        </el-select>

        <span class="control-label">宽度:</span>
        <el-input-number
            v-model="params.width"
            :min="100"
            size="small"
            type="number"
            :disabled="saveIng"
            
            style="width: 110px;"

        ></el-input-number>
        <span class="unit">px</span>

        <span class="control-label">高度:</span>
        <el-input-number
            v-model="params.height"
            :min="100"
            size="small"
            :disabled="saveIng"
            type="number"
            
            style="width: 110px;"

        ></el-input-number>
        <span class="unit">px</span>

        <span class="control-label">背景颜色:</span>
        <el-select
            v-model="params.background"
            size="small"
            style="width: 90px; margin-right: 10px;"
            :disabled="saveIng"
        >
          <el-option
              v-for="color in backgrounds"
              :key="color.value"
              :label="color.label"
              :value="color.value"
          >
            <span :style="{ background: color.value }" class="color-swatch"></span>
            {{ color.label }}
          </el-option>
        </el-select>

        <el-button
            type="primary"
            size="small"
            :loading="imgInfo.snapping"
            :disabled="imgInfo.snapping || !imgInfo.img || saveIng"
            @click="removeBg()"
        >
          {{ imgInfo.snapping ? '抠图中...' : '开始抠图' }}
        </el-button>


      </div>

      <div class="main-content-wrapper">
        <el-row :gutter="0">
          <el-col :xs="24" :sm="12" class="preview-area">
            <div
                class="canvas-wrapper"

            >
              <el-upload
                  action="#"
                  :show-file-list="false"
                  :auto-upload="false"
                  :on-change="handleUploadChange"
                  accept="image/*"
                  class="upload-overlay"
              >
                <img
                    v-if="imgInfo.img"
                    :src="imgInfo.img"
                    class="original-image-preview"
                    alt="Uploaded Original Image"
                />
                <div v-else class="upload-text">
                  <i class="el-icon-picture-outline"></i>
                  <p>点击选择图片</p>
                </div>
              </el-upload>

              <div v-if="false" class="loading-overlay">
                <i class="el-icon-loading"></i>
                <p>正在进行 AI 抠图...</p>
              </div>
            </div>
          </el-col>


          <el-col :xs="24" :sm="12" class="placeholder-area">
            <div v-if="imgInfo.snapImg">
<!--              <span class="control-label">高度:</span>-->
<!--              <el-input-number-->
<!--                  v-model="params.height"-->
<!--                  :min="100"-->
<!--                  size="small"-->
<!--                  :disabled="saveIng"-->
<!--                  type="number"-->
<!--                  -->
<!--                  style="width: 110px;"-->

<!--              ></el-input-number>-->
<!--              <span class="unit">px</span>-->

              <span class="control-label">宽:</span>
              <el-input-number
                  v-model="afterImgStyle.width"
                  @change="changeAfterWidth"
                  size="small"
                  :disabled="saveIng"
                  type="number"
                  style="width: 110px;"

              ></el-input-number>
              <span class="unit">px</span>

              <span class="control-label">左:</span>
              <el-input-number
                  v-model="afterImgStyle.left"
                  size="small"
                  :disabled="saveIng"
                  type="number"
                  style="width: 110px;"

              ></el-input-number>
              <span class="unit">px</span>

              <span class="control-label">上:</span>
              <el-input-number
                  v-model="afterImgStyle.top"
                  size="small"
                  :disabled="saveIng"
                  type="number"
                  
                  style="width: 110px;"

              ></el-input-number>
              <span class="unit">px</span>

              <el-button
                  type="success"
                  size="small"
                  :loading="saveIng"
                  :disabled="saveIng"
                  @click="saveFile()"
              >
                另存为
              </el-button>
            </div>

            <div id="card-photo-container" style="position: relative;overflow: hidden;display: block;margin: 0;padding: 0"

                :style="{
              width: params.width + 'px',
              height: params.height + 'px',
              background : params.background,
            }">
              <img
                  style="position: absolute;display: block;margin: 0;padding: 0;"
                  v-if="imgInfo.snapImg"
                  :src="imgInfo.snapImg"
                  @load="loadSnapImg"
                  :style="{
                    width: afterImgStyle.width + 'px',
                    height: afterImgStyle.height + 'px',
                    top : afterImgStyle.top + 'px',
                    left : afterImgStyle.left + 'px',
                  }"
                  class=""
                  alt="扣图后的"
              />
              <div v-else class="info-text">
                <p>证件照标准信息和操作提示</p>
                <p>当前尺寸: {{ params.width }}x{{ params.height }} px</p>
                <p>当前背景: {{ params.background }}</p>
                <p v-if="!isPreloaded" style="color: orange; font-weight: bold;">模型正在后台加载...</p>
                <div v-if="imgInfo.snapping">
                  <p>用时:{{ (imgInfo.endTime - imgInfo.startTime)/1000 }}秒</p>
                  <p>说明:{{ imgInfo.msg }}</p>
                  <p v-if="imgInfo.imgError.length > 0">错误:{{ imgInfo.imgError }}</p>
                </div>
              </div>
            </div>


          </el-col>
        </el-row>
      </div>

      <div class="bottom-controls" v-if="false">
        <div class="control-row">
          <span class="control-label">缩放:</span>
          <el-slider

              :min="0"
              :max="100"
              :step="0.01"

              style="width: 250px; margin: 0 15px;"
          ></el-slider>
          <span class="control-label">{{ (0 * 100).toFixed(0) }}%</span>

          <el-divider direction="vertical"></el-divider>

          <span class="control-label">位置:</span>
          <el-button-group class="position-group">
            <el-button size="mini" icon="el-icon-top" ></el-button>
            <el-button size="mini" icon="el-icon-bottom" ></el-button>
            <el-button size="mini" icon="el-icon-left" ></el-button>
            <el-button size="mini" icon="el-icon-right" ></el-button>
          </el-button-group>

          <el-button size="mini"  style="margin-left: 20px;">重置</el-button>
        </div>
      </div>

    </el-card>

    <table>
      <tr>
        <td>模型</td>
        <td>已加载</td>
        <td>总</td>
        <td>进度</td>
      </tr>
      <tr v-for="(item,k) of loadModelProcess " :key="k">
        <td>{{ item.key }}</td>
        <td>{{ item.current }}</td>
        <td>{{ item.total }}</td>
        <td>{{ item.progress }}</td>
      </tr>
    </table>
  </div>
</template>

<style scoped>
.card-photo-container {
  padding: 10px;
  min-width: 300px;
}
.box-card {
  box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1);
}
/* --- 1. 顶部控制栏样式 --- */
.top-controls {
  padding: 10px;
  border-bottom: 1px solid #ebeef5;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
}
.control-label {
  margin-left: 10px;
  margin-right: 5px;
  font-size: 13px;
  color: #606266;
  white-space: nowrap;
}
.unit {
  font-size: 13px;
  margin-right: 15px;
}
.color-swatch {
  display: inline-block;
  width: 12px;
  height: 12px;
  border-radius: 2px;
  vertical-align: middle;
  margin-right: 5px;
  border: 1px solid #ccc;
}
/* --- 2. 主体内容区样式 --- */
.main-content-wrapper {
  min-height: 450px;
}
.preview-area {
  padding: 20px;
  border-right: 1px solid #ebeef5;
}
.placeholder-area {
  padding: 20px;
  transition: background-color 0.3s;
}
.info-text {
  padding: 50px 20px;
  text-align: center;
  color: greenyellow;
}
/* Canvas/Image 容器 */
.canvas-wrapper {
  position: relative;
  touch-action: none;
  cursor: default; /* 默认使用默认光标 */
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: auto;
  min-height: 350px;
}
/* 拖动状态时才改变光标 */
.canvas-wrapper.is-dragging {
  cursor: grabbing;
}
.canvas-wrapper:not(.is-dragging) .photo-canvas {
  cursor: grab;
}

/* 原图预览样式：用 <img/> 负责即时预览 */
.original-image-preview {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  object-fit: contain; /* 保持图片比例，充满容器 */
  max-width: 100%;
  max-height: 100%;
  border: 1px solid #ccc;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 5;
}

/* Canvas 样式 (只在抠图后显示) */
.photo-canvas {
  max-width: 100%;
  height: auto;
  border: 1px solid #ccc;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: box-shadow 0.1s;
}
.is-dragging .photo-canvas {
  box-shadow: 0 0 15px rgba(64, 158, 255, 0.8);
}
/* 上传覆盖层 */
.upload-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: rgba(255, 255, 255, 0.8);
  cursor: pointer;
  border: 2px dashed #d9d9d9;
  color: #999;
  z-index: 20;
}
.upload-text {
  text-align: center;
}
.upload-text i {
  font-size: 40px;
  margin-bottom: 10px;
}

/* 抠图中状态提示 */
.loading-overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: rgba(255, 255, 255, 0.9);
  z-index: 15;
  color: #409EFF;
  font-size: 16px;
  font-weight: bold;
}
.loading-overlay i {
  font-size: 40px;
  margin-bottom: 10px;
}

.hidden-upload {
  display: none;
}

/* --- 3. 底部控制栏样式 --- */
.bottom-controls {
  padding: 8px 10px;
  border-top: 1px solid #ebeef5;
  background-color: #f5f5f5;
  display: flex;
  justify-content: center;
  align-items: center;
}
.control-row {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
}
.position-group {
  margin-left: 10px;
}


/* --- 移动端适配 (覆盖原有的适配样式) --- */
@media screen and (max-width: 768px) {
  .card-photo-container {
    padding: 0;
  }

  .top-controls, .control-row {
    justify-content: center;
    padding: 10px 5px;
  }
  .top-controls > * {
    margin-bottom: 5px;
  }

  /* ⭐ 修正 1: 移除 border-right，让左右区域的视觉连接更自然 */
  .preview-area {
    border-right: none;
    padding-bottom: 0; /* 减少间距，让内容更紧凑 */
  }

  /* ⭐ 修正 2: 移除 display: none; 让右侧占位区域显示 */
  .placeholder-area {
    /* display: none; 这行被移除 */
    display: block; /* 确保它显示 */
    padding-top: 5px; /* 调整顶部间距 */
    /* 移除 display: none; 后的样式微调 */
    padding-left: 20px;
    padding-right: 20px;
  }

  .bottom-controls {
    padding: 10px 0;
  }
  .control-row {
    justify-content: space-around;
  }

  /* 调整 El-col 的基础 padding，确保内容不贴边 */
  .main-content-wrapper .el-col {
    padding: 10px !important; /* 覆盖行内或 El-Row 的 gutter 效果 */
  }
}

</style>