import FileUtil from "@/util/FileUtil";
import imgRemoveBackground, { preload } from '@imgly/background-removal';

class MattingUtil
{
    //加载进度
    #progress = {};

    #isPreloaded = false;

    getProcess()
    {
        return this.#progress;
    }

    getIsPreloaded()
    {
        return this.#isPreloaded;
    }

    loadModel()
    {
        if (window.__PRERENDER_INJECTED__)
        {
            this.#isPreloaded = true;
            return;
        }
        let config = {
            debug: true,// enable or disable useful console.log outputs
            proxyToWorker: true, // Whether to proxy the calculations to a web worker. (Default true)
            model: 'small', // The model to use. (Default "medium")
            output: {
                format: 'image/png', // The output format. (Default "image/png")
                quality: 1, // The quality. (Default: 0.8)
                type: 'foreground', // The output type. (Default "foreground")
            },
            progress: (key, current, total) => {
                if (this.#progress[key] === undefined)
                {
                    this.#progress[key] = {
                        key: key,
                        current: 0,
                        total: 0,
                    };
                }
                this.#progress[key] = {
                    key: key,
                    current:  FileUtil.formatBytes(current),
                    total: FileUtil.formatBytes(total),
                    progress : (current * 100/total)+'%',
                };
                console.log(`Downloading ${key}: ${current} of ${total}`);
            }
        };
        //预加载
        preload(config).then(() => {
            this.#isPreloaded = true;
            console.log("Background removal resources preloaded");
        });
    }

    async removeBg(img) {
        const blob = await imgRemoveBackground(img);
        return {
            blob : blob,
            url : URL.createObjectURL(blob),
        }
    }

    async snap(item) {
        try {
            if (item.snapImg !== null) {
                //扣完了
                return;
            }
            if (item.imgError) {
                item.msg = '选择的图片文件有误';
                return;
            }
            if (!item.img) {
                item.msg = '没上传'
                return;
            }
            if (item.snapping) {
                return;
            }
            console.log(30);
            item.msg = '扣图中...'
            item.snapping = true;
            item.startTime = (new Date()).getTime();
            item.timer = setInterval(() => {
                item.endTime = (new Date()).getTime();
            });
            const blob = await imgRemoveBackground(item.img)

            //const blob = item.imgFile;


            item.timer = null;
            item.endTime = (new Date()).getTime();
            // The result is a blob encoded as PNG. It can be converted to an URL to be used as HTMLImage.src
            item.snapImg = URL.createObjectURL(blob);
            item.snapImgFile = blob;
            item.snapping = false;
            item.msg = '完成';
        } catch (e) {
            item.msg = e.msg
        } finally {
            if (item.timer) {
                clearInterval(item.timer);
                item.timer = null;
            }
        }
        return item;
    }

}
const mattingUtil = new MattingUtil()
mattingUtil.loadModel();
export default mattingUtil;